<template>
  <div class="login-wrap" id="indexLizi">
    <div class="login-title"></div>
    <el-form v-show="!isShow" :model="loginForm" :rules="loginRules" ref="loginForm"  class="login-container">
      <el-button class="change" type="text" @click="isShow=true">注册</el-button>
      <div class="title">登录</div>
      <el-form-item prop="name">
       <el-input type="text" placeholder="admin" :maxlength="20" size="large" v-model="loginForm.name" > </el-input>
      </el-form-item>
      <el-form-item prop="pwd">
       <el-input placeholder="●●●●●●" type="password" v-model="loginForm.pwd" size="large" :maxlength="20"> </el-input>
      </el-form-item >
      <el-form-item style="width:100%;" class="login-btn">
         <el-button size="large" style="width:100%;" @click.native.prevent="handleSubmit":loading="logining">{{logintext}}</el-button>
      </el-form-item>
    </el-form>
<!-- 注册 -->
    <el-form v-show="isShow" :model="registerForm" :rules="registerRules" ref="registerForm"  class="login-container">
      <el-button class="change" @click="isShow=false" type="text">登录</el-button>
      <div class="title">注册</div>
      <el-form-item prop="name">
        <el-input type="text" placeholder="名字" :maxlength="20" size="large" v-model="registerForm.name" > </el-input>
      </el-form-item>
      <el-form-item prop="pwd">
        <el-input placeholder="密码" type="password" v-model="registerForm.pwd" size="large" :maxlength="20"> </el-input>
      </el-form-item>
      <el-form-item  prop="email" :rules="[{ required: true, message: '请输入邮箱地址', trigger: 'blur' },
      { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change' }]">
        <el-input placeholder="邮箱" v-model="registerForm.email"></el-input>
      </el-form-item>
      <el-form-item style="width:100%;" class="login-btn">
        <el-button type="primary" size="large" style="width:100%;" @click.native.prevent="registerSubmit" :loading="logining">{{registertext}}</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="text/ecmascript-6">
import {mapMutations, mapGetters} from 'vuex'
export default {
  data() {
    var validatename = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入！'))
      } else {
        callback();
      }
    }
    return {
      isShow: false,
      logining: false,
      logintext: '登录',
      registertext: '注册',
      loginForm: {
        name: '',
        pwd:''
      },
      loginRules: {
        name: [
          { required: true, validator: validatename, trigger: 'blur' }
        ],
        pwd: [
          { required: true, message: '请输入秘密！', trigger: 'blur' },
          { min: 6, max: 16, message: 'must be over 6 numbers', trigger: 'blur' }
        ]
      },
      registerForm: {
        name:'',
        pwd:''
      },
      registerRules:{
        name: [
          { required: true, validator: validatename, trigger: 'blur' }
        ],
        pwd: [
          { required: true, message: '请输入秘密！', trigger: 'blur' },
          { min: 6, max: 16, message: 'must be over 6 numbers', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    ...mapMutations({
      setUser: 'SET_USER'
    }),
    registerSubmit(){
      var _this = this;
      this.$refs.registerForm.validate((valid) => {
        if (valid) {
          _this.logining = true;
          _this.registertext = "Wait...";
          this.$ajax.post('/api/c/register',
            _this.registerForm
            ).then(function(response){
              if(0==response.data.code){
                _this.registertext = "注册...";
                setTimeout(function () {
                  _this.logining = false;
                  _this.$message({message: response.data.data,type: 'success'});
                  _this.$refs['registerForm'].resetFields();
                }, 1200);
              }else if(1==response.data.code){
                _this.$message({message: response.data.msg,type: 'error'});
              }
            }).catch(function(error){
            console.log(error);
          }); 
        } else { return false;}
        setTimeout(() => {
          _this.logining = false;
          _this.registertext = "注册";
        }, 3000);
      })
    },
    handleSubmit() {
      var _this = this;
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          _this.logining = true;
          _this.logintext = "Wait...";
          this.$ajax.post('/api/c/login',
            _this.loginForm
            ).then(function(response){
              if(0==response.data.code){
                _this.logintext = "Loging...";
                setTimeout(function () {
                _this.logining = false;
                _this.$message({message: '登录成功',type: 'success'});
                sessionStorage.setItem('u', JSON.stringify(response.data.data))
                _this.setUser(JSON.parse(sessionStorage.getItem('u')))
                _this.$router.push({ path: '/userinfo' });},1000);
              };
            }).catch(function(error){console.log(error)}); 
        } else { return false;}
        setTimeout(() => {
          _this.logining = false;
          _this.logintext = "Enter";
        }, 3000);
      });
    }
  }
}

</script>

<style scoped>
.change{
  right:12px;
  top: 2px;
  color:#7bbaf4;
  position:absolute;
}
.login-title {
  text-align: center;
  padding: 50px 20px 30px;
  font-size: 32px;
  font-weight: 700;
  text-shadow: 0 2px 2px rgba(0,0,0, .4);
  letter-spacing: 3px;
  color: #fff;
  margin: 50px 0px 40px 0px;
}
.login-container {
  border-radius: 5px;
  width: 350px;
  max-width: 90%;
  padding: 20px 30px;
  background: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
  margin: 0 auto;
  position: relative;
  z-index: 10;
  }
.login-wrap {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
@media (max-width: 480px){
    padding:10px;
    width:auto;
    background: none;
    box-shadow: none;
    .title{
      display: none;
    } 
}
.title {
    text-align: center;
    color: #888;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 25px;
    
  }
.login-btn{
    /* padding-top: 10px; */
    margin-bottom: 12px;
}
.el-button--large{
   padding:15px;
   border-radius: 5px;
   background: #63B8FF;
   color:#fff;
}
.el-button--large:hover {
   background: #5d99e6;
   border-color:#4d99e6;
}
</style>


